
.btnc{
  background-color: #F9B131;
  height: 20px;
  width: 50%;
  margin:10px auto;
  padding: 20px;
  font-size: 20px;
  line-height: 16px;
  border-radius: 30px;
  cursor:pointer;
  margin-left: 0px;
  transition: 0.3s;
  float: right;
}
.btnc button{
  font-size: 20px;
  color: #0B4D8A;
  width: 100%;
   cursor:pointer;
   margin-left: -150px;
   transition: ease 0.5s;
}
.btnc:hover button {
  margin-left:20px;
}



